.background--white {
    background: #FFFFFF;
}


.slider {
    margin-top: 100px;
    margin-bottom: 60px;
    &__page {
        background-color: #F5F5FA;
        border-radius: 12px;
        // display: flex;
        // flex-direction: column;
    }
    &__info {
        padding: 30px;
    }
    &__pic {
        height: 500px;
        width: 100%;
        object-fit: cover;
        border-radius: 12px 12px 0px 0px;
    }
    &__link {
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #24A3FF;
        &::after {
            content: '';
            border: solid #24A3FF;
            border-width: 0 3px 3px 0;
            display: inline-block;
            padding: 3px;
            transform: rotate(-45deg);
            -webkit-transform: rotate(-45deg);
            margin-left: 3px;
        }
    }
    &__name {
        font-weight: 700;
    font-size: 38px;
    line-height: 46px;
    background: linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    }
    &__text {
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        color: #B3B3BA;
        &-extra {
            font-size: 14px;
            line-height: 22px;
            color: #B3B3BA;
        }
    }
    
}

